import React from "react"
import Grid from "@material-ui/core/Grid"
import { useStaticQuery, graphql } from "gatsby"
import Img from 'gatsby-image'
import WorkExCard from './workExCards'
import Image from './image'

function About() {
  const data = useStaticQuery(graphql`
    query {
      allFile(filter: { relativeDirectory: { eq: "workExImages" } }) {
        edges {
          node {
            id
            childImageSharp {
              fluid {
                base64
                tracedSVG
                srcWebp
                srcSetWebp
                originalImg
                originalName
              }
            }
          }
        }
      }
    }
  `)

  console.log(data)

  return (
    <section id="about">
      <Grid
        container
        alignItems="center"
        direction="row"
        justify="center"
        spacing={3}
        style={{ width: "100%" }}
      >
        <Grid item lg={6} md={6} sm={12}>
          <h3 className="color-primary text-center">About me?</h3>
          <div className="about-content">
            <p className="color-secondary text-center">
              Well, I'm Divyansh, a 3<sup>rd</sup>-year CS undergraduate student
              at IIT, Indore. I am passionate about Web Development and Hardware
              Design. I also enjoy Competitive Programming and have been
              selected for ICPC Regionals in 2018 and 2019.
            </p>
          </div>
        {/* <WorkExCard image={data.allFile.edges[0].node.childImageSharp.fluid} /> */}
        </Grid>

        <Grid item lg={6} md={6} sm={12}>
            <Img fluid={data.allFile.edges[0].node.childImageSharp.fluid} />
            <Image />
        </Grid>
      </Grid>
    </section>
  )
}

export default About
